// menu
$(function () {
	$("#hamburger").on("click", function (event) {
		event.preventDefault();
		openSideMenu();
	});
	const openSideMenu = () => {
		$("#sidemenu").addClass("show");
	};

	$(".sidemenu_close_btn").on("click", function (event) {
		event.preventDefault();
		closeSideMenu();
	});
	const closeSideMenu = () => {
		$("#sidemenu").removeClass("show");
	};
});
